import React from 'react';
import profilepie from '../../assets/profile-pie.png';

function ProfileCompletion(props) {
    return (
    <div className="row pd-l-5">
        <div className="wd-pct-90 ">
                <div className="profile-completion-header">Profile Completion Status</div>
            <div className="row pd-l-5 ">
                <div className="wd-pct-14 ">
                    <div className="profile-completion-card">
                    Expertise
                    </div>
                    <div className="profile-progress">
                        <div className="profile-progress-bar" style={{width:'40%'}}></div>                    
                    </div>
                </div>
                <div className="wd-pct-14">
                    <div className="profile-completion-card">
                    Employment
                    </div>
                    <div className="profile-progress">
                        <div className="profile-progress-bar" style={{width:'20%'}}></div>                        
                    </div>
                </div>
                <div className="wd-pct-14">
                    <div className="profile-completion-card">
                    Qualification
                    </div>
                    <div className="profile-progress">
                        <div className="profile-progress-bar" style={{width:'90%'}}></div>                        
                    </div>
                </div>
                <div className="wd-pct-14">
                    <div className="profile-completion-card">
                    Accomplishments
                    </div>
                    <div className="profile-progress">
                        <div className="profile-progress-bar" style={{width:'40%'}}></div>                        
                    </div>
                </div>                
                <div className="wd-pct-14">
                    <div className="profile-completion-card">
                   Recommendations
                    </div>
                    <div className="profile-progress">
                        <div className="profile-progress-bar" style={{width:'40%'}}></div>                        
                    </div>
                </div>
                <div className="wd-pct-14 ">
                    <div className="profile-completion-card">
                    Extracurriculars
                    </div>
                    <div className="profile-progress">
                        <div className="profile-progress-bar" style={{width:'40%'}}></div>                        
                    </div>
                </div>
                <div className="wd-pct-14 b-r-border">
                    <div className="profile-completion-card">
                    Contact
                    </div>
                    <div className="profile-progress">
                        <div className="profile-progress-bar" style={{width:'100%'}}></div>                        
                    </div>
                </div>                
            </div>
            </div>
            <div className="wd-pct-10 text-center pd-t-20">
                    {/*<div className="profile-completion-card-total b-r-border">
                    60%
                    </div>
                    <div className="profile-progress">
                        <div className="profile-progress-bar-total" style={{width:'60%'}}></div>                        
                    </div>*/}
                    <img src={profilepie} style={{height: '80px'}}  />                            
                </div>
    </div>
        
    );
  }

export default ProfileCompletion