import React, { useState } from 'react';
import { Modal } from 'reactstrap';
import { Collapse } from 'reactstrap';
import {
    BrowserRouter as Router, Switch, Route, Link , useHistory
  } from "react-router-dom";
import { SelectDD , ExternalDD } from '../../common/dropdowns';

const skillData = [ // Example Datastructure, Value should be unique, label should be set to display text.
    { value: 'example1', label: 'Example1' },
    { value: 'example2', label: 'Example2' },
  ]
const EditMyEduModal = ({ modal, toggle }) => {
    var history = useHistory();
    const onNavigate = (o) => {
      history.push(o)
    }

    const onDDClick = (o) => {
      console.log("DD CLICK", o)
      history.push(o.value)
    }
    const [selected, setSelected] = useState(skillData[0]);
    const [collapse, setCollapse] = useState(false);
    const togglecollapse = () => setCollapse(!collapse);
    return (
    <Modal isOpen={modal} toggle={toggle} className="modal-wrap wd-800">        
        <div className="pd-t-20 pd-r-20 pd-l-20 pd-b-10 v-align box-shadow" >
            <div className="modal-header">Edit Extracurricular Interests</div>
            <div className="v-align f-right" style={{marginLeft:'auto'}}>
                <i className="far fa-question-circle modal-close mg-r-15" ></i>            
                <i className="fas fa-times modal-close" onClick={toggle}></i>
            </div>
        </div>   
        <hr className="mg-r-0"></hr>
        <div className="modal-block">
            <div className="modal-subheader">Add an Extracurricular Interest</div>            
            <div className="row mg-b-35">
                <div className="col-md-6">
                    <input type="text" className="form-control"></input>
                </div>
                <div className="col-md-6">
                    <button className="btn btn-primary">
                        <i className="fas fa-plus mg-r-5"></i>Add Interest
                        <span className="button-info">3</span>
                    </button>
                </div>
            </div>
            <ul className="feed-blog">
                <li className="active-feed">                
                <div class="feed-btn btn btn-primary btn-outline-primary btn-icon" onClick={togglecollapse}>
                    <i className="fas fa-school"></i>
                </div>
                <h6 className="v-align-bottom">
                    <div className="wd-pct-70">Swimming</div>                    
                    <div className="wd-pct-15 " >     
                        <button className="btn btn-mini btn-default mg-r-10 ">
                            <i className="far fa-eye"></i>
                        </button>               
                        <button className="btn btn-mini btn-default ">
                            <i className="fas fa-trash-alt"></i>
                        </button>                    
                    </div>
                </h6>
                <Collapse isOpen={!collapse}>
                <div className="mg-b-5">
                <div className="modal-subheader">Title Held</div>                
                <div className="mg-b-15"><input type="text" className="form-control"></input></div>
                <div className="mg-b-10">
                    <button className="btn btn-small btn-default">
                        <i className="fas fa-plus mg-r-5"></i>Add Achievement
                        <span className="button-info">3</span>
                    </button>
                </div>
                <div className="b-border pd-10 mg-t-10 mg-b-10">
                    <div className="row">
                        <div className="col-md-9">
                            <div className="modal-sublistheader">Description</div>
                            <div><input type="text" className="form-control"></input></div>
                        </div>
                        <div className="col-md-3">
                            <div className="modal-sublistheader">Year</div>
                            <div><input type="text" className="form-control"></input></div>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-md-6">
                            <div className="modal-sublistheader">Location</div>
                            <div><input type="text" className="form-control"></input></div>
                        </div>
                        <div className="col-md-3">
                            <div className="modal-sublistheader">City/State</div>
                            <div><input type="text" className="form-control"></input></div>
                        </div>
                        <div className="col-md-3">
                            <div className="modal-sublistheader">Country</div>
                            <div><input type="text" className="form-control"></input></div>
                        </div>
                    </div>   
                    <div className="row mg-t-15">
                        <div className="col-md-8">
                        
                            <button className="btn btn-mini btn-primary mg-r-25">
                                <i className="fas fa-info mg-r-5"></i>Upload Honours
                            </button>                        
                            <i className="fas fa-certificate text-c-success mg-r-10 c-pointer"></i>
                            <i className="fas fa-certificate text-c-success mg-r-10 c-pointer"></i>
                            <i className="fas fa-photo-video text-c-warning c-pointer"></i>
                        
                        </div>
                        <div className="col-md-4">
                        <div className="row f-right mg-r-5">
                            <div>
                                <i className="far fa-question-circle text-c-primary-active c-pointer mg-r-5 "></i>
                            </div>
                            <div className="text-small-primary">Supported documents</div>
                        </div>
                        </div>
                    </div>                   
                </div>
                </div>
            </Collapse>
            </li>
          </ul>         
        </div> 
        <hr className="mg-0 "></hr>
        <div className="pd-20 box-shadow-top">
            <button className="btn btn-danger-lite f-right " onClick={toggle}>Cancel</button>             
            <button className="btn btn-primary-lite f-right mg-r-15">Save</button>
            <button className="btn btn-success-lite mg-r-15 f-right">Reset</button>            
        </div>
                 
    </Modal>
)}
export default EditMyEduModal;