import React, { useState } from 'react';
import { Collapse } from 'reactstrap';

function Recommendations(props) {
    const [collapse, setCollapse] = useState(false);
    const toggle = () => setCollapse(!collapse);
    return (
    <div className="mg-t-20" >
        <div className="card" >
            <div className="card-header">
                <div className="card-header-left">
                    <h5>Recommendations</h5>
                </div>
                <div className="card-header-right">
                    <ul className="row" >                        
                        <li><i className="far fa-edit"></i></li>   
                        <li><i className={collapse ? "fa fa-chevron-down" : "fa fa-chevron-up"} onClick={toggle}></i></li>                                 
                    </ul>
                </div>
            </div>
            <Collapse isOpen={!collapse}>            
            <div className="card-block">
                <div className="row">
                    <div className="col-md-2 card-subtitle">Primary
                        
                    </div>
                    <div className="col-md-3 card-subtitle">Category
                        
                    </div>
                    <div className="col-md-3 card-subtitle">Skill
                        
                    </div>
                    <div className="col-md-2 card-subtitle">Expertise</div>
                    <div className="col-md-2 card-subtitle">Score
                        
                    </div>
                
                </div>
                </div>
            </Collapse>
    </div>
    </div>
        
    );
  }

export default Recommendations