import React from 'react';
import MyIntroduction from './MyIntroduction';
import MyProfileCompletion from './MyProfileCompletion';
import MySkill from './MySkill';
import MyExp from './MyExp';
import MyQual from './MyQualification';
import MyAccomplishments from './MyAccomplishments';
import MyRecommendations from './MyRecommendations';
import MyExtracurriculars from './MyExtracurriculars';
import MyContact from './MyContact';

function CreateProfile(props) {
    return (
    
        <div className="page-wrapper">          
            <MyIntroduction />
            <MyProfileCompletion />
            <MySkill />
            <MyExp/>
            <MyQual/>
            <MyAccomplishments/>
            <MyRecommendations/>
            <MyExtracurriculars/>
            <MyContact/>            
        </div>
    
    );
  }

export default CreateProfile