import alt from './alt'
import Actions from './action'
import _ from 'lodash'

var bindListenersObject = {};
for (var i in Actions)
    if (typeof Actions[i] === "function")
        bindListenersObject[i] = Actions[i];

export default alt.createStore({
    displayName: 'store',
    progressModel: undefined,
    publicMethods: {},
    bindListeners: bindListenersObject,
    state: {
        route: "home",
        sidebarExpand: false,
    },
    storeSetState(o) {
        this.setState(o)
    },
})