import React, { useState } from 'react';
import { Modal } from 'reactstrap';
import {
    BrowserRouter as Router, Switch, Route, Link , useHistory
  } from "react-router-dom";
import { SelectDD , ExternalDD } from '../../common/dropdowns';

const EditMyContactModal = ({ modal, toggle }) => {
    return (
    <Modal isOpen={modal} toggle={toggle}  className="modal-wrap wd-700"> 
       {/* wd-x , x works from 0 to 1000 in multiples of 10. so other widths please make class  */}
        <div className="d-flex pd-t-20 pd-r-20 pd-l-20 pd-b-10 v-align box-shadow" >
            <div className="modal-header">Edit Contact Information</div>
            <div className="v-align f-right" style={{marginLeft:'auto'}}>
                <i className="far fa-question-circle modal-close mg-r-15" ></i>            
                <i className="fas fa-times modal-close" onClick={toggle}></i>
            </div>
        </div>   
        <hr className="mg-r-0"></hr>
        <div className="modal-block">
            <div >                
                <div className="modal-subheader">Address
                    <span className="mandatory-field">*</span>
                </div>
                <div className="modal-sublistheader">House Name and Number</div>
                <div><input type="text" className="form-control"></input></div>  
                <div className="modal-sublistheader">Street Address 1</div>
                <div><input type="text" className="form-control"></input></div>
                <div className="modal-sublistheader">Street Address 2</div>
                <div><input type="text" className="form-control"></input></div> 
                <div className="row">
                    <div className="col-md-6">   
                    <div className="modal-sublistheader">City/State</div>
                    <div><input type="text" className="form-control"></input></div> 
                    </div>
                    <div className="col-md-6">
                    <div className="modal-sublistheader">Zip Code</div>
                    <div><input type="text" className="form-control"></input></div>
                    </div>   
                </div>   
                <div className="row">
                    <div className="col-md-6">   
                    <div className="modal-sublistheader">State</div>
                    <div><input type="text" className="form-control"></input></div> 
                    </div>
                    <div className="col-md-6">
                    <div className="modal-sublistheader">Country</div>
                    <div><input type="text" className="form-control"></input></div>
                    </div>   
                </div>                     
            </div> 
        </div> 
        <hr className="mg-0 "></hr>
        <div className="modal-block">          
            <div className="mg-t-10">
                <div className="modal-subheader">e-mail
                    <span className="mandatory-field">*</span>
                </div> 
                <div><input type="text" className="form-control"></input></div>          
            </div>
        </div>
        <hr className="mg-0 "></hr>
        <div className="modal-block">
            <div className="mg-t-10 mg-b-15">
                <div className="v-align">
                    <div className="modal-subheader">Phone
                        <span className="mandatory-field">*</span>
                    </div> 
                    <div className="f-right mg-b-5" style={{marginLeft:'auto'}}><button className="btn btn-small btn-primary">
                    <i className="fas fa-plus mg-r-5"></i>Add Phone</button></div>
                </div>
                <div><input type="text" className="form-control"></input></div>          
            </div>      
        </div>  
            <hr className="mg-0 "></hr>
        <div className="pd-20 box-shadow-top">
            <button className="btn btn-danger-lite f-right " onClick={toggle}>Cancel</button>             
            <button className="btn btn-primary-lite f-right mg-r-15">Save</button>
            <button className="btn btn-success-lite mg-r-15 f-right">Reset</button>            
        </div>
                 
    </Modal>
)}
export default EditMyContactModal;